import React, { Component } from "react";
import config from "../../config";
import symbols from "../../currencySymbols";

import axios from "axios";
import "./accounts.css";

class Account extends Component {
  constructor(props) {
    super(props);
    this.state = {
      accounts: {},
      loading: true,
      balances: {},
      transactions: {}
    };
  }
  componentDidMount = () => {
    let clientId = localStorage.getItem("clientId");
    if (clientId) {
      this.loadAccounts(localStorage.getItem("clientId"));
    } else {
      this.setState({
        notification: "unable to load client id"
      });
    }
  };

  loadAccounts = clientId => {
    let that = this;
    axios({
      method: "get",
      url: config.ACCOUNT_URL + clientId,
      responseType: "json"
    })
      .then(resp => {
        that.state.accounts = resp.data;
        that.loadBalances(resp.data);
      })
      .catch(e => {
        console.log("exception while loading accounts ", e);
      });
  };

  loadBalances = data => {
    let that = this;
    let accountObj = {};
    data.map((account, index) => {
      return axios({
        method: "get",
        url: config.BALANCE_URL + account.accountId,
        responseType: "json"
      })
        .then(resp => {
          accountObj[account.accountId] = resp.data;
          if (data.length - 1 === index) {
            that.state.balances = accountObj;
            this.loadTransactions(data);
          }
        })
        .catch(e => {
          console.log("exception while loading balances ", e);
        });
    });
  };

  loadTransactions = data => {
    let that = this;
    let accountObj = {};
    data.map((account, index) => {
      return axios({
        method: "get",
        url: config.TRANSACTIONS_URL + account.accountId,
        responseType: "json"
      })
        .then(resp => {
          accountObj[account.accountId] = resp.data;
          that.state.transactions = accountObj;
          if (data.length - 1 === index) {
            setTimeout(() => {
              this.setState({
                loading: false
              });
            }, 1000);
          }
        })
        .catch(e => {
          console.log("exception while loading transactions ", e);
        });
    });
  };

  getCurrencySymbol = currency => {
    if (symbols[currency])
      return <span dangerouslySetInnerHTML={{ __html: symbols[currency] }} />;
    else return currency;
  };

  renderTransactions = accountID => {
    return (
      <div>
        {this.state.transactions &&
          this.state.transactions[accountID] &&
          this.state.transactions[accountID].map((transactions, index) => {
            return (
              <div className="transactions-row" key={"transaction_" + index}>
                <span>{transactions.productRef}</span>
                <span className="text-right">
                  {this.getCurrencySymbol(transactions.accountDetails.currency)}{" "}
                  {transactions.notional}
                </span>
              </div>
            );
          })}
      </div>
    );
  };

  render() {
    if (this.state.loading === true) return <div>Loading...</div>;
    let accountsCard = null;
    if (this.state.accounts) {
      accountsCard = (
        <div className="row">
          {this.state.accounts.map((account, index) => {
            return (
              <div className="col-sm-4" key={index}>
                <div className="card">
                  <div className="card-body">
                    <h5 className="card-title">{account.currency} account</h5>
                    <div className="card-text">
                      {this.state.balances[account.accountId].currency}{" "}
                      {this.state.balances[account.accountId].cashBalance}
                      {this.renderTransactions(account.accountId)}
                    </div>
                  </div>
                </div>
              </div>
            );
          })}
        </div>
      );
    }
    return <div>{accountsCard}</div>;
  }
}

export default Account;
