import React, { Component } from "react";
import "./header.css";

class Header extends Component {
  render() {
    return (
      <header className="header">
        {this.props.title}
        {this.props.showLogout && (
          <a href="#/" className="text-light float-right">
            Logout
          </a>
        )}
      </header>
    );
  }
}

export default Header;
